import { useSelector } from '../../services/hooks';
import { useParams } from 'react-router';

import styles from './ingredient-details.module.css';

type TIngredientId = {id: string};

function IngredientDetails(){
	const { id }: TIngredientId = useParams();
	const { loading, error, ingredients } = useSelector(
		state => state.ingredients);
	const ingredient = ingredients.find(item => item._id === id);

	return(
		<section className={styles.container}>
			{ loading && <p className="text text_type_main-medium mt-20">...</p> }
			{ error && <p className="text text_type_main-medium mt-20"> !</p> }
			{ 
			ingredient
			? ( 
			<>
				<span className="text text_type_main-large"> </span>
				<img src={ingredient.image_large} alt={ingredient.name} className="mb-4"/>
				<p className="text text_type_main-medium mb-8" data-testid="ingredient-details-name">{ingredient.name}</p>
				<section className={styles.composition}>
					<div className={styles.compositionItem}>
						<p className="text text_type_main-small">, </p>
						<p className="text text_type_digits-default">{ingredient.calories}</p>
					</div>
					<div className={styles.compositionItem}>
						<p className="text text_type_main-small">, </p>
						<p className="text text_type_digits-default">{ingredient.proteins}</p>
					</div>
					<div className={styles.compositionItem}>
						<p className="text text_type_main-small">, </p>
						<p className="text text_type_digits-default">{ingredient.fat}</p>
					</div>
					<div className={styles.compositionItem}>
						<p className="text text_type_main-small">, </p>
						<p className="text text_type_digits-default">{ingredient.carbohydrates}</p>
					</div>
				</section>
			</>	)
			: ( <p className="text text_type_main-medium mt-20">  !</p> )
			}
		</section>
	)
}

export default IngredientDetails